<template>
  <div class="burn-form">
    <div class="burn-form__container">
      <h2 class="burn-form__title">Burn Form</h2>
      
      <!-- Wallet Connection -->
      <div class="burn-form__wallet-section">
        <div v-if="!walletConnected" class="burn-form__connect-wallet">
          <p class="burn-form__connect-description">Connect your wallet to burn BITCAT tokens</p>
          <button @click="connectWallet" class="burn-form__connect-button" :disabled="loading">
            <i class="fas fa-wallet"></i>
            Connect Phantom Wallet
          </button>
        </div>
        
        <div v-else class="burn-form__wallet-info">
          <div class="burn-form__wallet-connected">
            <i class="fas fa-check-circle"></i>
            <span>Wallet Connected</span>
            <button @click="disconnectWallet" class="burn-form__disconnect-button">
              disconnect <i class="fas fa-times"></i>
            </button>
          </div>
          <p class="burn-form__wallet-address">{{ formatAddress(walletAddress) }}</p>
          <div class="burn-form__balance">
            <span>BITCAT Balance: {{ formatAmount(bitcatBalance) }} tokens</span>
          </div>
        </div>
      </div>

      <!-- Burn Form -->
      <div v-if="walletConnected" class="burn-form__form">
        <div class="burn-form__input-group">
          <label for="burnAmount" class="burn-form__label">Amount to Burn</label>
          <div class="burn-form__input-container">
            <input
              id="burnAmount"
              v-model="burnAmount"
              type="text"
              :max="bitcatBalance"
              placeholder="Enter amount"
              class="burn-form__input"
              :disabled="loading"
              @input="handleAmountInput"
              @keypress="handleKeyPress"
              autocomplete="off"
            />
            <span class="burn-form__currency">BITCAT</span>
          </div>
          <div class="burn-form__input-actions">
            <button @click="setBurnAmount(bitcatBalance * 0.25)" class="burn-form__preset-button">25%</button>
            <button @click="setBurnAmount(bitcatBalance * 0.5)" class="burn-form__preset-button">50%</button>
            <button @click="setBurnAmount(bitcatBalance * 0.75)" class="burn-form__preset-button">75%</button>
            <button @click="setBurnAmount(bitcatBalance)" class="burn-form__preset-button">MAX</button>
          </div>
        </div>

        <div v-if="burnAmount && burnAmount > 0" class="burn-form__summary">
          <div class="burn-form__summary-item">
            <span>Amount to Burn:</span>
            <span class="burn-form__summary-value">{{ formatAmount(burnAmount) }} BITCAT</span>
          </div>
          <div class="burn-form__summary-item">
            <span>Remaining Balance:</span>
            <span class="burn-form__summary-value">{{ formatAmount(bitcatBalance - burnAmount) }} BITCAT</span>
          </div>
        </div>

        <button
          @click="burnTokens"
          :disabled="!canBurn || loading"
          class="burn-form__burn-button"
        >
          <span v-if="!loading" class="burn-form__button-content">
            <i class="fas fa-fire"></i>
            Burn {{ formatAmount(burnAmount) }} BITCAT
          </span>
          <span v-else class="burn-form__loading">
            <i class="fas fa-spinner fa-spin"></i>
            {{ loadingText }}
          </span>
        </button>

        <div v-if="error" class="burn-form__error">
          <i class="fas fa-exclamation-triangle"></i>
          {{ error }}
        </div>
      </div>
    </div>

    <!-- Toast Component -->
    <AppToast
      :show="toast.show"
      :message="toast.message"
      :type="toast.type"
      @close="toast.show = false"
      :duration="toast.duration"
    />
  </div>
</template>

<script>
import { ref, computed, onMounted } from 'vue';
import { PublicKey, Transaction } from '@solana/web3.js';
import axios from 'axios';
import store from '@/vuex';
import AppToast from '@/components/Toast.vue';

export default {
  name: 'BurnForm',
  components: {
    AppToast
  },
  emits: ['burn-complete', 'burn-prepared'],
  setup(props, { emit }) {
    // Toast state
    const toast = ref({
      show: false,
      message: '',
      type: 'info',
      duration: 3000
    });

    // Function to show toast messages
    const showToast = (message, type = 'info') => {
      toast.value.message = message;
      toast.value.type = type;
      toast.value.show = true;
    };

    const formatAmount = computed(() => {
      return (value) => {
        if (!value) return '0';
        return value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',');
      };
    });
    
    const walletAddress = ref('');
    const walletConnected = ref(false);
    const bitcatBalance = ref(0);
    const burnAmount = ref('');
    const loading = ref(false);
    const loadingText = ref('');
    const error = ref('');

    const canBurn = computed(() => {
      return burnAmount.value > 0 && 
             burnAmount.value <= bitcatBalance.value &&
             walletConnected.value &&
             !loading.value;
    });

    const connectWallet = async () => {
      try {
        error.value = '';
        loading.value = true;
        loadingText.value = 'Connecting wallet...';

        // Check if Phantom is installed
        if (!window.solana || !window.solana.isPhantom) {
          throw new Error('Phantom wallet not found. Please install Phantom extension.');
        }

        // Connect to wallet
        await window.solana.connect();
        walletAddress.value = window.solana.publicKey.toString();
        walletConnected.value = true;
        
        showToast('Wallet connected successfully!', 'success');
        
        // Load BITCAT balance
        await loadBitcatBalance();
        
      } catch (err) {
        error.value = err.message;
        console.error('Wallet connection error:', err);
        showToast(`Wallet connection failed: ${err.message}`, 'error');
      } finally {
        loading.value = false;
        loadingText.value = '';
      }
    };

    const disconnectWallet = () => {
      walletAddress.value = '';
      walletConnected.value = false;
      bitcatBalance.value = 0;
      burnAmount.value = '';
      error.value = '';
      
      if (window.solana) {
        window.solana.disconnect();
      }
      
      showToast('Wallet disconnected', 'info');
    };

    const loadBitcatBalance = async () => {
      try {
        if (!walletAddress.value) {
          bitcatBalance.value = 0;
          return;
        }

        const response = await axios.get(`/dapp/get-bitcat-balance/${walletAddress.value}`);
        
        if (response.data.balance) {
          // ÐšÐ¾Ð½Ð²ÐµÑ€Ñ‚Ð¸Ñ€ÑƒÐµÐ¼ Ð¸Ð· lamports Ð² Ñ‚Ð¾ÐºÐµÐ½Ñ‹ (9 decimals)
          bitcatBalance.value = response.data.balance;
        } else {
          bitcatBalance.value = 0;
        }
        
      } catch (err) {
        console.error('Error loading BITCAT balance:', err);
        bitcatBalance.value = 0;
      }
    };

    const setBurnAmount = (amount) => {
      burnAmount.value = Math.floor(amount).toString(); // Round down to whole number and convert to string
    };

    const burnTokens = async () => {
      try {
        error.value = '';
        loading.value = true;
        loadingText.value = 'Preparing burn transaction...';

        // Step 1: Prepare burn transaction
        const prepareResponse = await axios.post('/token-burn/prepare-transaction', {
          walletAddress: walletAddress.value,
          amount: parseFloat(burnAmount.value)
        });

        if (!prepareResponse.data.success) {
          throw new Error(prepareResponse.data.message);
        }

        const { transaction: serializedTransaction, amountLamports } = prepareResponse.data;
        
        emit('burn-prepared', { amountLamports });

        loadingText.value = 'Please sign the transaction in your wallet...';

        // Step 2: Sign transaction with Phantom
        const transaction = Transaction.from(Buffer.from(serializedTransaction, 'base64'));
        const signedTransaction = await window.solana.signTransaction(transaction);

        loadingText.value = 'Confirming burn transaction...';

        // Step 3: Confirm transaction
        const confirmResponse = await axios.post('/token-burn/confirm-transaction', {
          walletAddress: walletAddress.value,
          signedTransaction: Buffer.from(signedTransaction.serialize()).toString('base64'),
          amountLamports
        });

        if (!confirmResponse.data.success) {
          throw new Error(confirmResponse.data.message);
        }

        const result = confirmResponse.data;
        
        showToast(`Successfully burned BITCAT tokens!`, 'success');
        
        // Reset form
        burnAmount.value = '';
        await loadBitcatBalance(); // Refresh balance
        
        emit('burn-complete', result);

      } catch (err) {
        error.value = err.response?.data?.message || err.message;
        console.error('Burn error:', err);
        showToast(`Burn failed: ${error.value}`, 'error');
      } finally {
        loading.value = false;
        loadingText.value = '';
      }
    };

    const formatAddress = (address) => {
      if (!address) return '';
      return `${address.slice(0, 4)}...${address.slice(-4)}`;
    };

    const formatNumber = (num) => {
      if (!num) return '0';
      return Number(num).toFixed(2);
    };

    const handleAmountInput = (event) => {
      // Handle input validation
      let value = event.target.value;
      
      // Remove all non-digit and non-dot characters
      value = value.replace(/[^0-9.]/g, '');
      
      // Prevent multiple dots
      const dotCount = (value.match(/\./g) || []).length;
      if (dotCount > 1) {
        const firstDotIndex = value.indexOf('.');
        value = value.substring(0, firstDotIndex + 1) + value.substring(firstDotIndex + 1).replace(/\./g, '');
      }
      
      // Limit decimal places to 6 (BITCAT has 6 decimals)
      const parts = value.split('.');
      if (parts[1] && parts[1].length > 6) {
        parts[1] = parts[1].substring(0, 6);
        value = parts.join('.');
      }
      
      burnAmount.value = value;
    };

    const handleKeyPress = (event) => {
      // Handle key press validation
      const key = event.key;
      const currentValue = event.target.value;
      
      // Allow digits, dot, backspace, delete, arrow keys, tab
      if (key.match(/[0-9]/) || 
          key === '.' || 
          key === 'Backspace' || 
          key === 'Delete' || 
          key === 'ArrowLeft' || 
          key === 'ArrowRight' || 
          key === 'Tab') {
        
        // Prevent multiple dots
        if (key === '.' && currentValue.includes('.')) {
          event.preventDefault();
        }
        
        return;
      }
      
      // Block all other keys
      event.preventDefault();
    };

    // Auto-connect if user is already authenticated
    onMounted(() => {
      if (store.getters.isAuthenticated && store.getters.walletAddress) {
        walletAddress.value = store.getters.walletAddress;
        walletConnected.value = true;
        loadBitcatBalance();
      }
    });

    return {
      walletAddress,
      walletConnected,
      bitcatBalance,
      burnAmount,
      loading,
      loadingText,
      error,
      canBurn,
      connectWallet,
      disconnectWallet,
      setBurnAmount,
      burnTokens,
      formatAddress,
      formatNumber,
      toast,
      showToast,
      formatAmount,
      handleAmountInput,
      handleKeyPress
    };
  }
};
</script>

