<script>
import { ref, computed, onMounted, defineExpose, watch } from 'vue';
import axios from 'axios';

export default {
  name: 'BurnHistory',
  props: {
    refreshTrigger: {
      type: Boolean,
      default: false
    }
  },
  setup(props) {
    const burns = ref([]);
    const loading = ref(false);
    const error = ref('');
    const showModal = ref(false);
    const selectedBurn = ref(null);
    const pagination = ref({
      currentPage: 1,
      totalPages: 1,
      totalItems: 0,
      itemsPerPage: 20,
      hasNext: false,
      hasPrev: false
    });

    const visiblePages = computed(() => {
      const current = pagination.value.currentPage;
      const total = pagination.value.totalPages;
      const pages = [];
      
      // Show max 5 pages around current
      const start = Math.max(1, current - 2);
      const end = Math.min(total, current + 2);
      
      for (let i = start; i <= end; i++) {
        pages.push(i);
      }
      
      return pages;
    });

    const fetchBurnHistory = async (page = 1) => {
      try {
        loading.value = true;
        error.value = '';
        
        const response = await axios.get('/token-burn/history', {
          params: { page, limit: 20 }
        });
        
        if (response.data.success) {
          burns.value = response.data.data.burns;
          pagination.value = response.data.data.pagination;
        } else {
          error.value = response.data.message || 'Failed to load burn history';
        }
        
      } catch (err) {
        error.value = err.response?.data?.message || 'Error loading burn history';
        console.error('Error fetching burn history:', err);
      } finally {
        loading.value = false;
      }
    };

    const goToPage = (page) => {
      if (page >= 1 && page <= pagination.value.totalPages && page !== pagination.value.currentPage) {
        fetchBurnHistory(page);
      }
    };

    const formatBurnId = (burnId) => {
      return `${burnId.slice(0, 8)}...`;
    };

    const formatTokenAmount = (amount) => {
      return Math.floor(Number(amount)).toLocaleString('en-US');
    };

    const formatWalletAddress = (address) => {
      return `${address.slice(0, 4)}...${address.slice(-4)}`;
    };

    const isTeamBurn = (wallet) => {
      return wallet === 'bitcat5mMocyMZJWmiJ62T8gtFWGrRDr99Nz1bXR3ud';
    };

    const formatDate = (dateString) => {
      const date = new Date(dateString);
      const now = new Date();
      const diffInMinutes = Math.floor((now - date) / (1000 * 60));
      
      if (diffInMinutes < 1) {
        return 'Just now';
      } else if (diffInMinutes < 60) {
        return `${diffInMinutes}m ago`;
      } else if (diffInMinutes < 1440) {
        return `${Math.floor(diffInMinutes / 60)}h ago`;
      } else if (diffInMinutes < 10080) {
        return `${Math.floor(diffInMinutes / 1440)}d ago`;
      } else {
        return date.toLocaleDateString('en-US', {
          year: 'numeric',
          month: 'short',
          day: 'numeric'
        });
      }
    };

    const formatFullDate = (dateString) => {
      const date = new Date(dateString);
      return date.toLocaleString('en-US', {
        year: 'numeric',
        month: 'long',
        day: 'numeric',
        hour: '2-digit',
        minute: '2-digit',
        second: '2-digit'
      });
    };

    const openModal = (burn) => {
      selectedBurn.value = burn;
      showModal.value = true;
    };

    const closeModal = () => {
      showModal.value = false;
      selectedBurn.value = null;
    };

    const updateHistory = () => {
      // Refresh history from first page
      fetchBurnHistory(1);
    };

    onMounted(() => {
      fetchBurnHistory();
    });

    // Watch for refresh trigger from parent
    watch(() => props.refreshTrigger, (newValue) => {
      if (newValue) {
        fetchBurnHistory(1);
      }
    });

    // Expose methods for parent component
    defineExpose({
      updateHistory
    });

    return {
      burns,
      loading,
      error,
      showModal,
      selectedBurn,
      pagination,
      visiblePages,
      fetchBurnHistory,
      goToPage,
      formatBurnId,
      formatTokenAmount,
      formatWalletAddress,
      formatDate,
      formatFullDate,
      isTeamBurn,
      openModal,
      closeModal
    };
  }
};
</script>

