<template>
  <div class="burn-page">
    <div class="burn-page__container">
      <!-- Hero Section -->
      <BurnHero :refresh-trigger="historyRefreshTrigger" />

      <!-- Burn Form -->
      <BurnForm 
        @burn-complete="handleBurnComplete"
        @burn-prepared="handleBurnPrepared"
      />

      <!-- How It Works Section -->
      <BurnHowItWorks />

      <!-- Burn History Section -->
      <BurnHistory :refresh-trigger="historyRefreshTrigger" />

      <!-- Success Modal -->
      <div v-if="showSuccessModal" class="burn-page__modal-overlay">
        <div class="burn-page__modal-content burn-page__success-modal">
          <div class="burn-page__success-header">
            <i class="fas fa-fire"></i>
            <h2>Tokens Burned Successfully!</h2>
            <button class="burn-page__close-button" @click="showSuccessModal = false">Ã—</button>
          </div>
          
          <div class="burn-page__success-body">
            <div class="burn-page__success-stats">
              <div class="burn-page__stat-item">
                <div class="burn-page__stat-value">{{ formatTokens(burnedAmount) }}</div>
                <div class="burn-page__stat-label">BITCAT Burned</div>
              </div>
              <div class="burn-page__stat-item">
                <div class="burn-page__stat-value">{{ formatId(burnId) }}</div>
                <div class="burn-page__stat-label">Burn ID</div>
              </div>
            </div>

            <div class="burn-page__success-tx">
              <p>Transaction: <a :href="`https://solscan.io/tx/${txSignature}`" target="_blank">View on Solscan <i class="fas fa-external-link-alt"></i></a></p>
            </div>
            
            <div class="burn-page__modal-info">
              <i class="fas fa-heart"></i>
              <p>Thank you for supporting BITCAT's long-term value!</p>
            </div>
          </div>
          
          <div class="burn-page__share-section">
            <h3>Share your burn!</h3>
            <p class="burn-page__share-description">Let the community know you're supporting BITCAT!</p>
            <button class="burn-page__twitter-share-button" @click="shareOnTwitter">
              <i class="fab fa-twitter"></i> Share on Twitter(X)
            </button>
          </div>
        </div>
      </div>

      <!-- Info Modal -->
      <div v-if="showInfoModal" class="burn-page__modal-overlay" @click.self="showInfoModal = false">
        <div class="burn-page__modal-content">
          <button class="burn-page__close-button" @click="showInfoModal = false">Ã—</button>
          <h2>Why Burn BITCAT?</h2>
          <div class="burn-page__info-content">
            <p>Token burning is a deflationary mechanism that permanently removes tokens from circulation, potentially increasing the value of remaining tokens.</p>
            <p>The BITCAT team regularly burns tokens using revenue from platform fees, and community members can participate to support the project's growth.</p>
            <p>Every burned token contributes to building a stronger, more valuable ecosystem for all holders.</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref } from 'vue';
import BurnHero from '@/components/burn/BurnHero.vue';
import BurnForm from '@/components/burn/BurnForm.vue';
import BurnHowItWorks from '@/components/burn/BurnHowItWorks.vue';
import BurnHistory from '@/components/burn/BurnHistory.vue';
import store from '@/vuex';

export default {
  name: 'BurnPage',
  components: {
    BurnHero,
    BurnForm,
    BurnHowItWorks,
    BurnHistory
  },
  setup() {
    const showSuccessModal = ref(false);
    const showInfoModal = ref(false);
    const burnedAmount = ref(0);
    const burnId = ref('');
    const txSignature = ref('');
    const historyRefreshTrigger = ref(false);

    const handleBurnPrepared = (data) => {
      console.log('Burn prepared:', data);
    };

    const handleBurnComplete = (result) => {
      burnedAmount.value = result.amountBurned;
      burnId.value = result.burnId;
      txSignature.value = result.signature;
      showSuccessModal.value = true;
      historyRefreshTrigger.value = true;
      
      // Reset trigger after a short delay
      setTimeout(() => {
        historyRefreshTrigger.value = false;
      }, 100);
    };

    const formatTokens = (amount) => {
      return Number(amount).toFixed(0);
    };

    const shareOnTwitter = () => {
      const text = `I just burned ${formatTokens(burnedAmount.value)} $BITCAT tokens to support the project's long-term value! ðŸ”¥ Join the burn at https://solanabitcat.com/#/burn${store.getters.isAuthenticated ? ` #BITCATburn` : ''}`;
      window.open(`https://twitter.com/intent/tweet?text=${encodeURIComponent(text)}`, '_blank');
    };

    const formatId = (id) => {
      return id.slice(0, 4) + '...' + id.slice(-4);
    };

    return {
      showSuccessModal,
      showInfoModal,
      burnedAmount,
      burnId,
      txSignature,
      handleBurnPrepared,
      handleBurnComplete,
      formatTokens,
      shareOnTwitter,
      formatId,
      historyRefreshTrigger
    };
  }
};
</script>

