        <div class="burn-hero__image">
          <img src="@/assets/bitcat-burn.png" alt="BITCAT Token Burn" class="burn-hero__img" />
        </div>
        
        <div class="burn-hero__stats">
          <div class="burn-hero__stat">
            <div class="burn-hero__stat-value">{{ formatNumber(totalBurned) }}</div>
            <div class="burn-hero__stat-label">Total BITCAT Burned</div>
            <div class="burn-hero__stat-info">{{ totalBurns }} burns total</div>
          </div>
        </div>
      </div>
      
      <div class="burn-hero__text">
        <h1 class="burn-hero__title">Burn BITCAT Tokens</h1>
        <p class="burn-hero__subtitle">Support long-term price growth</p>
        
        <div class="burn-hero__sections">
          <div class="burn-hero__section">
            <div class="burn-hero__section-header">
              <i class="fas fa-building"></i>
              <h3>Team Burns</h3>
            </div>
            <p>We burn BITCAT using revenue from platform fees:</p>
            <div class="burn-hero__links">
