                <i class="fas fa-image"></i>
                NFT Mint
              </router-link>
            </div>
          </div>

          <div class="burn-hero__section">
            <div class="burn-hero__section-header">
              <i class="fas fa-users"></i>
              <h3>Community Burns</h3>
            </div>
            <p>You can specify the amount of tokens you want to burn and contribute to BITCAT's deflationary mechanism. <strong>You will receive a badge and an NFT of the burning BITCAT.</strong></p>
          </div>

          <div class="burn-hero__section">
            <div class="burn-hero__section-header">
              <i class="fas fa-question-circle"></i>
              <h3>Need BITCAT?</h3>
            </div>
            <p>Use our <router-link to="/cashback" class="burn-hero__cashback-link">ATA Cashback tool</router-link> to get free SOL, then buy BITCAT tokens and burn them.</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, onMounted, watch } from 'vue';
import axios from 'axios';

export default {
  name: 'BurnHero',
  props: {
    refreshTrigger: {
      type: Boolean,
      default: false
    }
  },
  setup(props) {
    const totalBurned = ref(0);
    const totalBurns = ref(0);
    const loading = ref(false);

    const fetchBurnStats = async () => {
      try {
        loading.value = true;
        const response = await axios.get('/token-burn/stats');
        
        if (response.data.success && response.data.stats) {
          const stats = response.data.stats;
          totalBurned.value = stats.totalBurnedTokens;
          totalBurns.value = stats.totalBurns;
        }
        
      } catch (error) {
        console.error('Error fetching burn stats:', error);
        // Fallback to default values if API fails
        totalBurned.value = 0;
        totalBurns.value = 0;
      } finally {
        loading.value = false;
      }
    };

    const formatNumber = (num) => {
      return Math.floor(Number(num)).toLocaleString('en-US');
    };

    onMounted(() => {
      fetchBurnStats();
    });

    // Watch for refresh trigger from parent
    watch(() => props.refreshTrigger, (newValue) => {
      if (newValue) {
        fetchBurnStats();
      }
    });

    return {
      totalBurned,
      totalBurns,
      loading,
      formatNumber
    };
  }
};
</script>

